@use 'base/variables' as v;
@use 'base/mixins' as m;

.nav-app{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 2rem 0 1rem;
}

.home-button{
    img{
        height: calc(v.$height-navbar - 1rem);
        width: auto;
    }
}

.right-buttons {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    @include m.telefono {
        flex-direction: row;
        gap: 3rem;
    }
}

.overview-space {
    display: grid;
    grid-template-rows: calc(50vh - v.$height-navbar + v.$height-footer) calc(50vh - v.$height-footer);
    @include m.desktop {
        grid-template-rows: 1fr;
        grid-template-columns: 30% 70%;
    }
}

.sections-area {
    grid-row-start: 2;
    grid-row-end: 3;
    @include m.desktop {
        grid-row: 1;
        display: flex;
        grid-column-start: 1;
        grid-column-end: 2;
    }
}

.inspection-area {
    position: relative;
    grid-row-start: 1;
    grid-row-end: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include m.desktop {
        grid-row: 1;
        height: calc(100vh - v.$height-navbar);
    }
}

.visualization-area{
    height: calc(50vh - 60px) ;
    width: 100%;
    display: grid;
    place-items: center;
    padding: 0.5rem;
    @include m.desktop {
        height: calc(100vh - v.$height-footer - v.$height-navbar) ;
    }
}

.visualizer-container{
    position: relative;
    height: auto;
    width: auto;
    max-width: 100%;
    max-height: calc(50vh - 60px - 1rem);
    @include m.desktop {
        max-height: calc(90vh - v.$height-footer - v.$height-navbar);
    }
}

.image-section {
    height: fit-content;
    width: fit-content;
    margin: auto auto;
    img {
        height: calc(50vh - v.$height-footer - 15px);
        width: auto;
    }

    @include m.desktop {
        margin: 2rem auto;
        top: 50%;
        left: 50%;
        img{
            height: calc(100vh - v.$height-footer - v.$height-navbar - 10rem);
        }
    }
}

.sections-index {
    height: 40px;
    background-color: v.$oscuro;
    display: flex;
    justify-content: center;
    gap: 3rem;
    padding: 0.7rem;
    @include m.desktop {
        width: 15%;
        gap: 2rem;
        flex-direction: column;
        justify-content: start;
        align-items: center;
        padding: 2rem 1rem;
        height: calc(100vh - v.$height-navbar);
    }
}

.section-view {
    height: calc(50vh - 40px - v.$height-footer);
    background-color: v.$oscuro2;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include m.desktop {
        width: 85%;
        height: calc(100vh - v.$height-navbar);
    }
}

.section-button {
    display: flex;
    gap: 1rem;
    align-items: center;
    border-style: none;
    cursor: pointer;
    background-color: transparent;
    
    img {
        height: 2rem;
        width: auto;
    }

    p {
        color: v.$blanco;
        font-weight: 500;
    }

    @include m.desktop {
        flex-direction: column;
        align-items: center;
        gap: 0;
        padding-top: 1rem;
        padding-bottom: 1.5rem;
        p{
            font-size: calc(((100vw*0.3)*0.15)*0.17);
            font-weight: 600;
            margin-bottom: 0;
        }
        img{
            height: calc(((100vw*0.3)*0.15)*0.40);
            width: auto;
        }
    }
}

.--active-button{
    background-color: v.$oscuro2;
}

.agregar-boton {

    --webkit-box-shadow: 9px 9px 15px 0px rgba(0,0,0,0.25);
    --moz-box-shadow: 9px 9px 15px 0px rgba(0,0,0,0.25);
    box-shadow: 9px 9px 15px 0px rgba(0,0,0,0.25);


    border-style: none;
    cursor: pointer;
    background-color: v.$oscuro3;
    width: 100%;
    border-radius: 0.7rem;

    p{
        color: v.$blanco;
        font-weight: 500;
    }

}

.--ask-gb{
    height: 100vh;
    width: 100vw;
    background-color: rgba(120, 120, 120, 0.85);
    position: absolute;
    display: grid;
    place-items: center;
    top: 0;
}

.user-form {
    background-color: v.$gris-bg;
    border-radius: 1rem;
    margin: 1rem;
    display: flex;
    gap: 2rem;
    flex-direction: column;
    padding: 3rem;
    border: solid 0.5rem v.$oscuro3;
    max-width: 45rem;
    label {
        font-weight: 600;
        font-size: 2rem;
    }
    input{
        font-weight: 500;
        font-size: 2rem;
    }
    .user-form__buttons{
        display: flex;
        gap: 1rem;
        justify-content: flex-end;
    }
    button{
        border-style: none;
        cursor: pointer;
        color: #fff;
        font-weight: 600;
        padding: 1.2rem;
        border-radius: 0.25rem;
    }
    .cancel-button{
        background-color: red;
    }
    .accept-button{
        background-color: green;
    }
}

.ban-form{
    display: flex;
    gap: 1rem;
    label{
        font-weight: 400;
        color: v.$grisFont;
        font-size: 1.5rem;
    }
    align-self: flex-end;
}
