@use 'base/variables' as v;
@use 'base/mixins' as m;

.nav-bar {
    display: flex;
    align-items: center;
    height: v.$height-navbar;
    background: rgb(104,85,137);
    background: linear-gradient(90deg, rgba(104,85,137,1) 0%, rgba(124,84,132,1) 31%, rgba(37,62,103,1) 100%);
}

.nav-inicio {
    padding: 2rem 2rem;
}

.icon-text-button{
    a {
        width: fit-content;
        text-decoration: none;
        color: v.$blanco;
        display: flex;
        gap: 0.8rem;
        align-items: center;

    }
    img {
        height: 1.8rem;
        width: auto;
    }
    h4{
        margin: 0;
        font-weight: 300;
        font-size: 1.5rem;
        @include m.telefono {
            font-weight: 600;
            font-size: 2rem;
        }
    }
}

.contenido-principal{
    height: calc(100vh - v.$height-navbar - v.$height-footer);
    padding: 1rem 2.5rem;
}

.selector-archivos{
    --webkit-box-shadow: 9px 9px 15px 0px rgba(0,0,0,0.14);
    --moz-box-shadow: 9px 9px 15px 0px rgba(0,0,0,0.14);
    box-shadow: 9px 9px 15px 0px rgba(0,0,0,0.14);
    border: dashed 12px v.$azulPrimario;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: v.$blanco;
    height: 100%; 
    background-origin: content-box;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    padding: 2rem 2rem;
    text-align: center;
    @include m.tablet {
        h1 {
            font-size: 5rem;
            margin-bottom: 1rem;
        }
        h3 {
            font-size: 3rem;
            margin: 0;
        }
        h3:last-of-type{
            font-size: 2.5rem;
        }
    }
}

.custom-file-boton {

    --webkit-box-shadow: 9px 9px 15px 0px rgba(0,0,0,0.14);
    --moz-box-shadow: 9px 9px 15px 0px rgba(0,0,0,0.14);
    box-shadow: 9px 9px 15px 0px rgba(0,0,0,0.14);

    display: inline;
    font-weight: 500;
    letter-spacing: 0.2rem;
    font-size: 1.8rem;
    border-radius: 0.7rem;
    background: rgb(37,62,103);
    background: linear-gradient(90deg, rgba(37,62,103,1) 0%, rgba(124,84,132,1) 69%, rgba(104,85,137,1) 100%);
    padding: 2.2rem 6rem;
    margin: 2.5rem 0 1rem 0;
    color: v.$blanco;
    cursor: pointer;
    @include m.tablet {
        font-size: 3rem;
    }
}

#input-file{
    display: none;
}

.footer {
    height: v.$height-footer;
    display: flex;
    justify-content: center;
}


.footer-content {
    display: flex;
    width: fit-content;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;

    h4{
        font-size: 1.3rem;
    }

    img{
        height: 1.5rem;
        width: auto;
    }
}