@use 'base/variables' as v;
@use 'base/mixins' as m;

.event-name-input{
    margin-bottom: 1rem !important;
}

.event-elements-viewer {
    display: block !important;
    margin-bottom: 0 !important;
    gap: 0;
}

.event-type-containers {
    padding: 0 0.7rem 0.7rem 0.7rem;
    margin-bottom: 1rem !important;
}

.event-type-section {
    display: flex;
    width: 100%;
    justify-content: space-between;
    button {
        flex: 0 0 calc(33% - 1rem);
    }
    img{
        height: 2.5rem;
    }
}

.list-elements {
    border: solid 1px #b3b3b3;
    height: calc((100vh - v.$height-navbar) - 87.6px - 80px - 200px);
    min-height: 100px;
    position: relative;
}

.components-list{
    height: 100%;
    overflow: auto;
    overflow: overlay;
    &::-webkit-scrollbar{
        display: none;
    }
}

.event-button {
    height: 100%;
    padding: 1rem;
    display: flex;
    gap: 1rem;
    align-items: center;
    justify-content: center;
    border-style: none;
    cursor: pointer;
    background-color: transparent;
    flex-direction: column;
    img {
        height: calc((100vw)*0.05);
        width: auto;
    }
    
    p {
        color: v.$blanco;
        font-weight: 500;
        font-size: calc((100vw)*0.03);
    }

    @include m.phablet {
        flex-direction: row;
        img {
            height: 2.5rem;
        }
        p {
            font-size: unset;
        }
    }
    
    @include m.desktop {
        height: fit-content;
        flex-direction: column;
        align-items: center;
        gap: 0;
        padding-top: 1rem;
        padding-bottom: 1.5rem;
        p{
            font-size: calc(((100vw*0.3)*0.21)*0.16);
            font-weight: 600;
            margin-bottom: 0;
        }
        img{
            height: calc(((100vw*0.3)*0.20)*0.27);
            width: auto;
        }
    }
}

.summary {
    height: 6rem !important;
    position: relative !important;
}   

.summary-list{
    .summary:last-child{
        margin-bottom: 0;
    }
}

.count-label {
    background-color: v.$grisFont;
    height: 2rem;
    width: 3rem;
    text-align: center;
    color: #fff;
    position: absolute;
    top: 0;
    right: 0;
    border: 0.3rem;
    font-weight: 500;
}

.list-controler {
    background-color: v.$oscuro2;
    height: 2.7rem;
    width: 10rem;
    position: absolute;
    top: - 22px;
    right: 1rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-weight: 500;
    color: #fff;
    border: 1px solid #d5d5d5;
}

.controller-button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2.5rem;
    img{
        height: auto;
        width: 1.7rem;
        height: 1.7rem;
        cursor: pointer;
    }
}

.cover-up{
    background-color: rgba($color: #000000, $alpha: 0.22);
    position: absolute;
    height: calc(100% - 1rem);
    width: calc(100% - 2.2rem);
    top: 0;
}

.cover-up-grid{
    background-color: rgba($color: #000000, $alpha: 0.50);
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
}

.notification-panel{
    position: absolute;
    bottom: - 12rem;
    width: 100%;
    padding: 0 1rem;
    align-self: center;
    max-width: 50rem;
    @include m.desktop {
        bottom: unset;
        top: 0.5rem;
    }
}

.notification__card{
    position: relative;
    display: flex;
    justify-content: space-evenly;
    border-radius: 6px;
    margin-bottom: 0.5rem;
}

.notification__body {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    .notification__data{
        flex: 0 0 85%;
    }

    h3{
        color: #fff;
        font-weight: 800;
        margin-top: 1rem;
        margin-bottom: 0;
    }
    p{
        margin-top: 1rem;
        margin-bottom: 2rem;
        color:#fff;
    }
}

.notification__close-button{
    flex: 0 0 5%;
    display: grid;
    place-items: center;
    img{
        height: 1.5rem;
        width: 1.5rem;
        cursor: pointer;
    }
}

.notification__loading-bar{
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    height: 0.6rem;
    width: 100%;
    border-end-start-radius: 1.5rem;
}

.user__error{
    background-color: #fd5656;
    .notification__loading-bar{
        background-color: #a51818;
    }
}

.user__success{
    background-color: #0de202;
    .notification__loading-bar{
        background-color: #0d8607;
    }
}