@use 'base/variables' as v;
@use 'base/mixins' as m;

.workspace{
    display: grid;
    grid-template-rows: calc(50vh - v.$height-navbar + v.$height-footer) calc(50vh - v.$height-footer);
    @include m.desktop {
        grid-template-rows: 1fr;
        grid-template-columns: 30% 70%;
    }
}
.states-area {
    background-color: v.$oscuro2;
    height: calc(100vh - v.$height-navbar);
    padding: 1rem 1.7rem;
    display: flex;
    flex-direction: column;
    overflow: auto;
    overflow: overlay;
    &::-webkit-scrollbar{
        display: none;
    }
    @include m.desktop{
        grid-row-start: 1;
        grid-row-end: 2;
        grid-column-start: 1;
        grid-column-end: 2;
    }
    .agregar-boton{
        margin-top: 1.5rem;
    }
}

.header-states-area{
    display: grid;
    grid-template-columns: 90% 10%;
    justify-content: center;
    align-items: center;
    h2 {
        color: #fff;
        font-weight: 600;
        letter-spacing: normal;
        text-align: center;
        text-transform: initial;
        margin: 2.7rem 0 3rem 0;
    }
    img{
        height: auto;
        width: 2.7rem;
    }
    a {
        border-radius: 50%;
        height: 4rem;
        width: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 1rem;
    }
}

.formulario {
    fieldset{
        margin-bottom: 3rem;
        display: flex;
        align-items: center;
    }
    fieldset:last-child{
        margin-bottom: 2rem;
        display: grid;
        grid-template-columns: 85% 15%;
        gap: 4%;
    }
    legend {
        color: #fff;
        font-weight: 500;
        padding: 0 1rem;
    }
    label {
        color: #fff;
    }
    input[type="text"] {
        width: 100%;
        height: 3rem;
        background-color: #fff;
        font-weight: 600;
    }
    input[type="range"] {
        width: 100%;
        margin-right: 1rem;
    }
    input[type="color"]{
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        width: 5rem;
        height: 4rem;
        background-color: transparent;
        border: none;
        cursor: pointer;
    }
    input[type="color"]::-webkit-color-swatch {
        border: none;
    }
    input[type="color"]::-moz-color-swatch {
        border: none;
    }
}


.tools-section {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 1rem;
    @include m.desktop {
        gap: unset;
        grid-template-rows: unset;
    }
}

.tool-button {
    height: 100%;
    padding: 1rem;
    display: flex;
    gap: 1rem;
    align-items: center;
    justify-content: center;
    border-style: none;
    cursor: pointer;
    background-color: transparent;
    
    img {
        height: 5rem;
        width: auto;
    }
    
    p {
        color: v.$blanco;
        font-weight: 500;
    }
    
    @include m.desktop {
        height: fit-content;
        flex-direction: column;
        align-items: center;
        gap: 0;
        padding-top: 1rem;
        padding-bottom: 1.5rem;
        p{
            font-size: calc(((100vw*0.3)*0.21)*0.15);
            font-weight: 600;
            margin-bottom: 0;
        }
        img{
            height: calc(((100vw*0.3)*0.20)*0.40);
            width: auto;
        }
    }
}

.main-buttons{
    display: flex;
    flex-direction: column;
    @include m.desktop {
        flex-direction: row;
        gap: 1rem;
    }
}

.container{
    position: absolute;
    top: 0;
    height: 100%;
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    grid-template-rows: repeat(16, 1fr);
}

.grid-item {
    border: solid 2px rgba(255, 255, 255, 0.3);
}

.position-relative{
    position: relative;
}

.--active-button2{
    background-color: v.$oscuro3 !important;
}

.--list-view{
    background-color: v.$moradoPrimario;
}

.self-aling-center {
    align-self: center;
}

.state-list-viewer{
    height: 100%;
    width: 100%;
    margin-top: 2rem;
    overflow: auto;
    overflow: overlay;
    &::-webkit-scrollbar{
        display: none;
    }
}

.estado{
    position: relative;
    background-color: v.$gris-bg;
    display: flex;
    align-items: center;
    padding: 1rem;
    border-radius: 0.8rem;
    margin-bottom: 1rem;
    height: 9.5rem;
    transition: border 1ms ease-in-out;
    justify-content: space-evenly;
    cursor: pointer;
    .estado__encabezado{
        display: flex;
        align-items: center;
        height: 4rem;
        gap: 1rem;
        img{
            width: 3rem;
        }
    }

    .estado__color{
        flex: 0 0 calc(15% - 1rem);
        height: 65%;
    }
    .estado__data{
        display: flex;
        flex: 0 0 calc(60% - 1rem);
        height: 100%;
        width: 100%;
        flex-direction: column;
        justify-content: space-evenly;
        overflow: hidden;
        h3{
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
        }
        h4{
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
        }
    }
    .estado__buttons{
        display: flex;
        flex: 0 0 calc(10% - 1rem);
        height: 100%;
        width: 80%;
        gap: 2rem;
        align-items: center;
    }
    
    img {
        height: 40%;
        width: auto;
        filter: invert(74%);
    }

    h3{
        margin: 0;
    }
    h4 {
        margin: 0;
    }
    span {
        margin-left: 2rem;
    }
}

.--selected-state{
    border: 4px solid v.$grisFont;
}

.editing-state-label{
    font-weight: 600;
}

.--invisible-element{
    display: none !important;
}

.mensaje-error {
    color: red;
    font-weight: 600;
}

.mensaje-exito {
    color: greenyellow;
    font-weight: 600;
}

.not-selected {
    background-color: rgba($color: #000000, $alpha: 0.5) !important;
}

