html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 16px; /*1 rem = 10px*/
  font-family: "Montserrat", sans-serif;
  background-color: #FCFCFC;
}

h1 {
  font-weight: 800;
  text-transform: uppercase;
  margin: 0.5rem;
  letter-spacing: 0.65rem;
  color: #384571;
}

h3 {
  font-weight: 400;
  letter-spacing: 0.1rem;
  color: #A3A3A3;
}

h4 {
  font-weight: 600;
  font-size: 1.8rem;
}

.nav-bar {
  display: flex;
  align-items: center;
  height: 60px;
  background: rgb(104, 85, 137);
  background: linear-gradient(90deg, rgb(104, 85, 137) 0%, rgb(124, 84, 132) 31%, rgb(37, 62, 103) 100%);
}

.nav-inicio {
  padding: 2rem 2rem;
}

.icon-text-button a {
  width: fit-content;
  text-decoration: none;
  color: #ffffff;
  display: flex;
  gap: 0.8rem;
  align-items: center;
}
.icon-text-button img {
  height: 1.8rem;
  width: auto;
}
.icon-text-button h4 {
  margin: 0;
  font-weight: 300;
  font-size: 1.5rem;
}
@media (min-width: 480px) {
  .icon-text-button h4 {
    font-weight: 600;
    font-size: 2rem;
  }
}

.contenido-principal {
  height: calc(100vh - 60px - 50px);
  padding: 1rem 2.5rem;
}

.selector-archivos {
  --webkit-box-shadow: 9px 9px 15px 0px rgba(0,0,0,0.14);
  --moz-box-shadow: 9px 9px 15px 0px rgba(0,0,0,0.14);
  box-shadow: 9px 9px 15px 0px rgba(0, 0, 0, 0.14);
  border: dashed 12px #384571;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  height: 100%;
  background-origin: content-box;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  padding: 2rem 2rem;
  text-align: center;
}
@media (min-width: 769px) {
  .selector-archivos h1 {
    font-size: 5rem;
    margin-bottom: 1rem;
  }
  .selector-archivos h3 {
    font-size: 3rem;
    margin: 0;
  }
  .selector-archivos h3:last-of-type {
    font-size: 2.5rem;
  }
}

.custom-file-boton {
  --webkit-box-shadow: 9px 9px 15px 0px rgba(0,0,0,0.14);
  --moz-box-shadow: 9px 9px 15px 0px rgba(0,0,0,0.14);
  box-shadow: 9px 9px 15px 0px rgba(0, 0, 0, 0.14);
  display: inline;
  font-weight: 500;
  letter-spacing: 0.2rem;
  font-size: 1.8rem;
  border-radius: 0.7rem;
  background: rgb(37, 62, 103);
  background: linear-gradient(90deg, rgb(37, 62, 103) 0%, rgb(124, 84, 132) 69%, rgb(104, 85, 137) 100%);
  padding: 2.2rem 6rem;
  margin: 2.5rem 0 1rem 0;
  color: #ffffff;
  cursor: pointer;
}
@media (min-width: 769px) {
  .custom-file-boton {
    font-size: 3rem;
  }
}

#input-file {
  display: none;
}

.footer {
  height: 50px;
  display: flex;
  justify-content: center;
}

.footer-content {
  display: flex;
  width: fit-content;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}
.footer-content h4 {
  font-size: 1.3rem;
}
.footer-content img {
  height: 1.5rem;
  width: auto;
}

.nav-app {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 2rem 0 1rem;
}

.home-button img {
  height: calc(60px - 1rem);
  width: auto;
}

.right-buttons {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
@media (min-width: 480px) {
  .right-buttons {
    flex-direction: row;
    gap: 3rem;
  }
}

.overview-space {
  display: grid;
  grid-template-rows: calc(50vh - 60px + 50px) calc(50vh - 50px);
}
@media (min-width: 1024px) {
  .overview-space {
    grid-template-rows: 1fr;
    grid-template-columns: 30% 70%;
  }
}

.sections-area {
  grid-row-start: 2;
  grid-row-end: 3;
}
@media (min-width: 1024px) {
  .sections-area {
    grid-row: 1;
    display: flex;
    grid-column-start: 1;
    grid-column-end: 2;
  }
}

.inspection-area {
  position: relative;
  grid-row-start: 1;
  grid-row-end: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1024px) {
  .inspection-area {
    grid-row: 1;
    height: calc(100vh - 60px);
  }
}

.visualization-area {
  height: calc(50vh - 60px);
  width: 100%;
  display: grid;
  place-items: center;
  padding: 0.5rem;
}
@media (min-width: 1024px) {
  .visualization-area {
    height: calc(100vh - 50px - 60px);
  }
}

.visualizer-container {
  position: relative;
  height: auto;
  width: auto;
  max-width: 100%;
  max-height: calc(50vh - 60px - 1rem);
}
@media (min-width: 1024px) {
  .visualizer-container {
    max-height: calc(90vh - 50px - 60px);
  }
}

.image-section {
  height: fit-content;
  width: fit-content;
  margin: auto auto;
}
.image-section img {
  height: calc(50vh - 50px - 15px);
  width: auto;
}
@media (min-width: 1024px) {
  .image-section {
    margin: 2rem auto;
    top: 50%;
    left: 50%;
  }
  .image-section img {
    height: calc(100vh - 50px - 60px - 10rem);
  }
}

.sections-index {
  height: 40px;
  background-color: #111111;
  display: flex;
  justify-content: center;
  gap: 3rem;
  padding: 0.7rem;
}
@media (min-width: 1024px) {
  .sections-index {
    width: 15%;
    gap: 2rem;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    padding: 2rem 1rem;
    height: calc(100vh - 60px);
  }
}

.section-view {
  height: calc(50vh - 40px - 50px);
  background-color: #292929;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1024px) {
  .section-view {
    width: 85%;
    height: calc(100vh - 60px);
  }
}

.section-button {
  display: flex;
  gap: 1rem;
  align-items: center;
  border-style: none;
  cursor: pointer;
  background-color: transparent;
}
.section-button img {
  height: 2rem;
  width: auto;
}
.section-button p {
  color: #ffffff;
  font-weight: 500;
}
@media (min-width: 1024px) {
  .section-button {
    flex-direction: column;
    align-items: center;
    gap: 0;
    padding-top: 1rem;
    padding-bottom: 1.5rem;
  }
  .section-button p {
    font-size: 0.765vw;
    font-weight: 600;
    margin-bottom: 0;
  }
  .section-button img {
    height: 1.8vw;
    width: auto;
  }
}

.--active-button {
  background-color: #292929;
}

.agregar-boton {
  --webkit-box-shadow: 9px 9px 15px 0px rgba(0,0,0,0.25);
  --moz-box-shadow: 9px 9px 15px 0px rgba(0,0,0,0.25);
  box-shadow: 9px 9px 15px 0px rgba(0, 0, 0, 0.25);
  border-style: none;
  cursor: pointer;
  background-color: #404040;
  width: 100%;
  border-radius: 0.7rem;
}
.agregar-boton p {
  color: #ffffff;
  font-weight: 500;
}

.--ask-gb {
  height: 100vh;
  width: 100vw;
  background-color: rgba(120, 120, 120, 0.85);
  position: absolute;
  display: grid;
  place-items: center;
  top: 0;
}

.user-form {
  background-color: #FCFCFC;
  border-radius: 1rem;
  margin: 1rem;
  display: flex;
  gap: 2rem;
  flex-direction: column;
  padding: 3rem;
  border: solid 0.5rem #404040;
  max-width: 45rem;
}
.user-form label {
  font-weight: 600;
  font-size: 2rem;
}
.user-form input {
  font-weight: 500;
  font-size: 2rem;
}
.user-form .user-form__buttons {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
}
.user-form button {
  border-style: none;
  cursor: pointer;
  color: #fff;
  font-weight: 600;
  padding: 1.2rem;
  border-radius: 0.25rem;
}
.user-form .cancel-button {
  background-color: red;
}
.user-form .accept-button {
  background-color: green;
}

.ban-form {
  display: flex;
  gap: 1rem;
  align-self: flex-end;
}
.ban-form label {
  font-weight: 400;
  color: #A3A3A3;
  font-size: 1.5rem;
}

.workspace {
  display: grid;
  grid-template-rows: calc(50vh - 60px + 50px) calc(50vh - 50px);
}
@media (min-width: 1024px) {
  .workspace {
    grid-template-rows: 1fr;
    grid-template-columns: 30% 70%;
  }
}

.states-area {
  background-color: #292929;
  height: calc(100vh - 60px);
  padding: 1rem 1.7rem;
  display: flex;
  flex-direction: column;
  overflow: auto;
  overflow: overlay;
}
.states-area::-webkit-scrollbar {
  display: none;
}
@media (min-width: 1024px) {
  .states-area {
    grid-row-start: 1;
    grid-row-end: 2;
    grid-column-start: 1;
    grid-column-end: 2;
  }
}
.states-area .agregar-boton {
  margin-top: 1.5rem;
}

.header-states-area {
  display: grid;
  grid-template-columns: 90% 10%;
  justify-content: center;
  align-items: center;
}
.header-states-area h2 {
  color: #fff;
  font-weight: 600;
  letter-spacing: normal;
  text-align: center;
  text-transform: initial;
  margin: 2.7rem 0 3rem 0;
}
.header-states-area img {
  height: auto;
  width: 2.7rem;
}
.header-states-area a {
  border-radius: 50%;
  height: 4rem;
  width: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 1rem;
}

.formulario fieldset {
  margin-bottom: 3rem;
  display: flex;
  align-items: center;
}
.formulario fieldset:last-child {
  margin-bottom: 2rem;
  display: grid;
  grid-template-columns: 85% 15%;
  gap: 4%;
}
.formulario legend {
  color: #fff;
  font-weight: 500;
  padding: 0 1rem;
}
.formulario label {
  color: #fff;
}
.formulario input[type=text] {
  width: 100%;
  height: 3rem;
  background-color: #fff;
  font-weight: 600;
}
.formulario input[type=range] {
  width: 100%;
  margin-right: 1rem;
}
.formulario input[type=color] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 5rem;
  height: 4rem;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.formulario input[type=color]::-webkit-color-swatch {
  border: none;
}
.formulario input[type=color]::-moz-color-swatch {
  border: none;
}

.tools-section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 1rem;
}
@media (min-width: 1024px) {
  .tools-section {
    gap: unset;
    grid-template-rows: unset;
  }
}

.tool-button {
  height: 100%;
  padding: 1rem;
  display: flex;
  gap: 1rem;
  align-items: center;
  justify-content: center;
  border-style: none;
  cursor: pointer;
  background-color: transparent;
}
.tool-button img {
  height: 5rem;
  width: auto;
}
.tool-button p {
  color: #ffffff;
  font-weight: 500;
}
@media (min-width: 1024px) {
  .tool-button {
    height: fit-content;
    flex-direction: column;
    align-items: center;
    gap: 0;
    padding-top: 1rem;
    padding-bottom: 1.5rem;
  }
  .tool-button p {
    font-size: 0.945vw;
    font-weight: 600;
    margin-bottom: 0;
  }
  .tool-button img {
    height: 2.4vw;
    width: auto;
  }
}

.main-buttons {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .main-buttons {
    flex-direction: row;
    gap: 1rem;
  }
}

.container {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  grid-template-rows: repeat(16, 1fr);
}

.grid-item {
  border: solid 2px rgba(255, 255, 255, 0.3);
}

.position-relative {
  position: relative;
}

.--active-button2 {
  background-color: #404040 !important;
}

.--list-view {
  background-color: #7B5484;
}

.self-aling-center {
  align-self: center;
}

.state-list-viewer {
  height: 100%;
  width: 100%;
  margin-top: 2rem;
  overflow: auto;
  overflow: overlay;
}
.state-list-viewer::-webkit-scrollbar {
  display: none;
}

.estado {
  position: relative;
  background-color: #FCFCFC;
  display: flex;
  align-items: center;
  padding: 1rem;
  border-radius: 0.8rem;
  margin-bottom: 1rem;
  height: 9.5rem;
  transition: border 1ms ease-in-out;
  justify-content: space-evenly;
  cursor: pointer;
}
.estado .estado__encabezado {
  display: flex;
  align-items: center;
  height: 4rem;
  gap: 1rem;
}
.estado .estado__encabezado img {
  width: 3rem;
}
.estado .estado__color {
  flex: 0 0 calc(15% - 1rem);
  height: 65%;
}
.estado .estado__data {
  display: flex;
  flex: 0 0 calc(60% - 1rem);
  height: 100%;
  width: 100%;
  flex-direction: column;
  justify-content: space-evenly;
  overflow: hidden;
}
.estado .estado__data h3 {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.estado .estado__data h4 {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.estado .estado__buttons {
  display: flex;
  flex: 0 0 calc(10% - 1rem);
  height: 100%;
  width: 80%;
  gap: 2rem;
  align-items: center;
}
.estado img {
  height: 40%;
  width: auto;
  filter: invert(74%);
}
.estado h3 {
  margin: 0;
}
.estado h4 {
  margin: 0;
}
.estado span {
  margin-left: 2rem;
}

.--selected-state {
  border: 4px solid #A3A3A3;
}

.editing-state-label {
  font-weight: 600;
}

.--invisible-element {
  display: none !important;
}

.mensaje-error {
  color: red;
  font-weight: 600;
}

.mensaje-exito {
  color: greenyellow;
  font-weight: 600;
}

.not-selected {
  background-color: rgba(0, 0, 0, 0.5) !important;
}

.event-name-input {
  margin-bottom: 1rem !important;
}

.event-elements-viewer {
  display: block !important;
  margin-bottom: 0 !important;
  gap: 0;
}

.event-type-containers {
  padding: 0 0.7rem 0.7rem 0.7rem;
  margin-bottom: 1rem !important;
}

.event-type-section {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.event-type-section button {
  flex: 0 0 calc(33% - 1rem);
}
.event-type-section img {
  height: 2.5rem;
}

.list-elements {
  border: solid 1px #b3b3b3;
  height: calc(100vh - 60px - 87.6px - 80px - 200px);
  min-height: 100px;
  position: relative;
}

.components-list {
  height: 100%;
  overflow: auto;
  overflow: overlay;
}
.components-list::-webkit-scrollbar {
  display: none;
}

.event-button {
  height: 100%;
  padding: 1rem;
  display: flex;
  gap: 1rem;
  align-items: center;
  justify-content: center;
  border-style: none;
  cursor: pointer;
  background-color: transparent;
  flex-direction: column;
}
.event-button img {
  height: 5vw;
  width: auto;
}
.event-button p {
  color: #ffffff;
  font-weight: 500;
  font-size: 3vw;
}
@media (min-width: 550px) {
  .event-button {
    flex-direction: row;
  }
  .event-button img {
    height: 2.5rem;
  }
  .event-button p {
    font-size: unset;
  }
}
@media (min-width: 1024px) {
  .event-button {
    height: fit-content;
    flex-direction: column;
    align-items: center;
    gap: 0;
    padding-top: 1rem;
    padding-bottom: 1.5rem;
  }
  .event-button p {
    font-size: 1.008vw;
    font-weight: 600;
    margin-bottom: 0;
  }
  .event-button img {
    height: 1.62vw;
    width: auto;
  }
}

.summary {
  height: 6rem !important;
  position: relative !important;
}

.summary-list .summary:last-child {
  margin-bottom: 0;
}

.count-label {
  background-color: #A3A3A3;
  height: 2rem;
  width: 3rem;
  text-align: center;
  color: #fff;
  position: absolute;
  top: 0;
  right: 0;
  border: 0.3rem;
  font-weight: 500;
}

.list-controler {
  background-color: #292929;
  height: 2.7rem;
  width: 10rem;
  position: absolute;
  top: -22px;
  right: 1rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-weight: 500;
  color: #fff;
  border: 1px solid #d5d5d5;
}

.controller-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.5rem;
}
.controller-button img {
  height: auto;
  width: 1.7rem;
  height: 1.7rem;
  cursor: pointer;
}

.cover-up {
  background-color: rgba(0, 0, 0, 0.22);
  position: absolute;
  height: calc(100% - 1rem);
  width: calc(100% - 2.2rem);
  top: 0;
}

.cover-up-grid {
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
}

.notification-panel {
  position: absolute;
  bottom: -12rem;
  width: 100%;
  padding: 0 1rem;
  align-self: center;
  max-width: 50rem;
}
@media (min-width: 1024px) {
  .notification-panel {
    bottom: unset;
    top: 0.5rem;
  }
}

.notification__card {
  position: relative;
  display: flex;
  justify-content: space-evenly;
  border-radius: 6px;
  margin-bottom: 0.5rem;
}

.notification__body {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
}
.notification__body .notification__data {
  flex: 0 0 85%;
}
.notification__body h3 {
  color: #fff;
  font-weight: 800;
  margin-top: 1rem;
  margin-bottom: 0;
}
.notification__body p {
  margin-top: 1rem;
  margin-bottom: 2rem;
  color: #fff;
}

.notification__close-button {
  flex: 0 0 5%;
  display: grid;
  place-items: center;
}
.notification__close-button img {
  height: 1.5rem;
  width: 1.5rem;
  cursor: pointer;
}

.notification__loading-bar {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  height: 0.6rem;
  width: 100%;
  border-end-start-radius: 1.5rem;
}

.user__error {
  background-color: #fd5656;
}
.user__error .notification__loading-bar {
  background-color: #a51818;
}

.user__success {
  background-color: #0de202;
}
.user__success .notification__loading-bar {
  background-color: #0d8607;
}

