@use 'base/variables' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body{
    font-size: 16px;   /*1 rem = 10px*/
    font-family: v.$fuentePrincipal;
    background-color: v.$gris-bg;
}

h1{
    font-weight: v.$bold;
    text-transform: uppercase;
    margin: 0.5rem;
    letter-spacing: 0.65rem;
    color: v.$azulPrimario;
}

h3{
    font-weight: v.$regular;
    letter-spacing: 0.1rem;
    color: v.$grisFont;
}

h4{
    font-weight: v.$semibold;
    font-size: 1.8rem;
}